div.row .column:nth-child(6n) {
  float: left;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}





/* mixin for multiline */
.block-with-text {
    overflow: hidden;
    position: relative;
    max-height: 3.6em;
    padding-right: 1em;
    min-height: 3.6em;
  }
  .block-with-text:before {
    content: '...';
    position: absolute;
    right: 0;
    bottom: 0;
  }
  .block-with-text:after {
    content: '';
    position: absolute;
    right: 0;
    width: 1em;
    height: 1em;
    margin-top: 0.2em;
    background: white;
  }


  .block-with-description {
    overflow: hidden;
    position: relative;
    max-height: 4.6em;
    padding-right: 1em;
    min-height: 4.6em;
  }
  .block-with-description:before {
    content: '...';
    position: absolute;
    right: 0;
    bottom: 0;
  }
  .block-with-description:after {
    content: '';
    position: absolute;
    right: 0;
    width: 1em;
    height: 1em;
    margin-top: 0.2em;
    background: white;
  }
  
  .container {
    position: relative;
    text-align: center;
    color: white;
  }
 
  .bottom-right {
    position: absolute;
    top: 8px;
    right: 30px;
  }

  .card{
    height: 315px!important;
  }
  

.Modal {
    max-width: 768px;
    width: 100%;
    margin: 0 auto;
    position: fixed;
    left: 0;
    right: 0;
    top: 100px;
    background:  white;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0,0,0,.4);
    z-index: 1010;
  }
  
  .Modal-close {
    width: 50px;
    height: 50px;
    position: absolute;
    right: -25px;
    top: -25px;
    border-radius: 50%;
    border: none;
    background: #171c30;
    color: white;
    cursor: pointer;
  }
  
  .Modal-close:after {
    content: "X";
    font-weight: bold;
    font-size: 30px;
    margin-left: 15px!important;
  }


  .wrapper {
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(255,255,255,0.90);
    height: 100vh;
    width: 100vw;
    z-index: 999;
  }


  
